<template>
  <!-- 商店 -->
  <div class="shop">
    <div class="info">
      <div class="left">
        <user-info></user-info>
      </div>
      <div class="right">
        <img
          @click="goMyCoupon"
          class="my-goods mr-30"
          src="~@/assets/images/shop/my_goods@2x.png"
          alt=""
        />
      </div>
    </div>
    <div class="main">
      <div class="top"></div>
      <div class="content">
        <div class="coupon" v-for="item in listData" :key="item.id">
          <div class="hd">
            <div class="brand">
              <img :src="basePicPath + item.pic_url" alt="" />
            </div>
            <div class="name-count">
              <div class="name">{{ item.full_name }}</div>
              <div class="count">库存：{{ item.avaiable_num || 0 }}</div>
            </div>
          </div>
          <div class="bd">
            <div class="label">抢购价</div>
            <div class="stars">
              <div class="stars-label">星钻</div>
              <div class="stars-num">{{ item.price }}</div>
            </div>
            <div class="buy" @click="buy(item)">
              <img src="~@/assets/images/shop/shop_buy@2x.png" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isModalShow1" title="温馨提示">
      <div class="modal-content" slot="content">
        <div class="text mb-24">
          购买{{ selectedItem.full_name }}将消耗{{
            selectedItem.price
          }}星钻，是否确认消费？
        </div>
      </div>
      <div class="modal-footer-row-two" slot="footer">
        <img
          @click="cancel"
          src="@/assets/images/common/modal_btn_qx.png"
          alt=""
        />
        <img
          @click="confirm"
          src="@/assets/images/common/modal_btn_qd.png"
          alt=""
        />
      </div>
    </Modal>
    <Modal class="modal" :visible.sync="isModalShow2" :title="modalTitle">
      <div class="modal-content" slot="content">
        <div class="text mb-24">{{ contentText }}</div>
        <div v-if="contentSubText" class="sub-text mb-32">
          {{ contentSubText }}
        </div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          v-if="buySuccess"
          @click="cancel2"
          src="@/assets/images/common/modal_btn_zdl.png"
          alt=""
        />
        <img
          v-else
          @click="cancel3"
          src="@/assets/images/common/modal_btn_earn.png"
          alt=""
        />
      </div>
    </Modal>
  </div>
</template>

<script>
import Modal from '@/components/commonModal'
import UserInfo from '@/components/userInfo'
import Pop from '@/components/commonPop'
import config from '@/utils/config'

export default {
  name: 'Shop',
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      basePicPath: config.basePicPath,
      listData: [],
      nickname: this.$store.getters.nick_name,
      star: 3000,
      selectedItem: {},
      modalTitle: '提示', // 弹窗顶部文字
      contentText: '', // 弹窗内容
      contentSubText: '', // 弹窗副内容
      isModalShow1: false, // 将消耗XX兑换
      isModalShow2: false, // 星钻不足
      isModalShow3: false, // 消费成功
      isModalShow4: false, // 员工账号不能购买商品
      buySuccess: true
    }
  },
  components: {
    Modal,
    Pop,
    UserInfo
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      const res = await this.$request('get', '/activity/getgoodslist')
      this.listData = res.data
    },
    buy (item) {
      this.selectedItem = item
      this.isModalShow1 = true
    },
    cancel () {
      this.isModalShow1 = false
    },
    cancel2 () {
      this.isModalShow2 = false
    },
    cancel3 () {
      this.isModalShow2 = false
      this.$router.push({ name: 'home', params: { earnStar: '1' } })
    },
    async confirm () {
      await this.$request('post', '/user/buygoods', { uid: this.userInfo.uid, gid: this.selectedItem.id }, { noErrorMsg: true }).then(res => {
        this.buySuccess = true
        this.modalTitle = '消费成功'
        this.contentText = this.selectedItem.full_name
        this.contentSubText = '可在我的商品中查看'
        this.isModalShow1 = false
      }).catch(
        err => {
          this.buySuccess = false
          if (err.error_no === '700003') {
            this.modalTitle = '消费失败'
            this.contentText = '星钻不足'
            this.contentSubText = ''
          } else {
            this.modalTitle = '消费失败'
            this.contentText = err.error_info
            this.contentSubText = ''
          }
        }
      )
      this.isModalShow2 = true
    },
    goMyCoupon () {
      this.$router.push('coupon')
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.shop {
  background-image: url(~@/assets/images/shop/shop_bg@2x.png);
  background-size: 100%;
  .info {
    position: fixed;
    top: 30px;
    z-index: 4;
    width: 100%;
    .flex_func(row, space-between, center, nowrap);
    .left {
      .userinfo {
        width: 100%;
        height: 110px;
        padding: 22px 0 0 24px;
        .flex_func(row, flex-start, center, nowrwp);
        .user-left {
          height: 96px;
          width: 96px;
          // background: url(../assets/home/level_gold@2x.png);
          .bg-image("../assets/home/level_gold","png");
          .flex_func(row, center, center, nowrap);
          .head {
            width: 88px;
            height: 88px;
          }
        }
        .user-right {
          width: 235px;
          height: 88px;
          // background: grey;
          margin-left: 18px;
          .tbox {
            width: 100%;
            height: 43px;
            line-height: 43px;
            font-size: 24px;
            color: #445680;
            background: rgba(255, 255, 255, 0.65);
            border-radius: 16px;
            padding: 0 16px;
          }
          .user-star {
            margin-top: 2px;
            .flex_func(row, space-between, center, nowrap);
            .starimg {
              width: 41px;
              height: 35px;
            }
          }
        }
        .head {
          width: 88px;
          height: 88px;
        }
      }
    }
    .right {
      align-self: flex-end;
      .my-goods {
        display: block;
        width: 104px;
        height: 91px;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .main {
    padding-top: 118px;
    .top {
      width: 750px;
      height: 285px;
      background-image: url(~@/assets/images/shop/shop_top@2x.png);
      background-size: 110% 285px;
      background-position: center;
      background-repeat: no-repeat;
      position: relative;
    }
    .content {
      height: calc(100vh - 353px);
      overflow-y: auto;
      background-image: url(~@/assets/images/shop/shop_content@2x.png);
      background-repeat: repeat-y;
      background-position: center;
      background-size: 746px 100%;
      margin-top: -50px;
      padding-top: 50px;
      padding-bottom: 10px;
      .coupon {
        margin: 0 auto 23px;
        width: 686px;
        height: 216px;
        background-image: url(~@/assets/images/shop/coupon@2x.png);
        background-repeat: no-repeat;
        background-size: 100%;
        display: flex;
        align-items: center;
        .hd {
          flex: none;
          box-sizing: border-box;
          padding-top: 41px;
          padding-left: 51px;
          padding-right: 10px;
          width: 482px;
          height: 216px;
          display: flex;
          .brand {
            margin-right: 31px;
            width: 78px;
            flex: none;
            img {
              width: 78px;
              height: auto;
            }
          }
          .name-count {
            padding-top: 10px;
            .name {
              color: #5f2800;
              font-size: 30px;
              margin-bottom: 25px;
            }
            .count {
              color: #848484;
              font-size: 22px;
            }
          }
        }
        .bd {
          flex: none;
          width: 204px;
          padding-left: 15px;
          .label {
            font-size: 24px;
            color: #a3a2a2;
            margin-left: 10px;
            margin-bottom: 5px;
          }
          .stars {
            display: flex;
            align-items: flex-end;
            margin-left: 10px;
            margin-bottom: 9px;
            .stars-label {
              color: #5f2800;
              font-size: 20px;
              margin-right: 17px;
            }
            .stars-num {
              color: #ff4a43;
              font-size: 46px;
            }
          }
          .buy {
            width: 142px;
            img {
              width: 142px;
              height: 56px;
            }
          }
        }
      }
    }
  }
}
</style>



